@import url(./resets.css);

.container {
    background-color: #a3a3a3;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
}

.cv {
    position: relative;
    display: flex;
    width: 70vw;
    height: 90vh;
}

.submit {
    position: absolute;
    top: 10px;
    right: 10px;
    background-color: #4b4b4b;
    color: #fff;
    padding: 5px 10px;
    border-radius: 5px;
    cursor: pointer;
}

.profile {
    background-color: #a3a4ad;
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding: 20px 5px;
    overflow: auto;
    align-items: center;
}

.main {
    background-color: #fff;
    padding: 20px 10px;
    flex: 1;
    overflow: auto;
    display: flex;
    flex-direction: column;
    gap: 30px;
}

.section {
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.profile .row-cell:not(:only-child) {
    max-width: 80px;
}

.section-title {
    border-bottom: 6px solid #8a8787;
    padding-bottom: 4px;
}

.profile .section-title {
    border-bottom: 4px solid #8a8787;
    font-size: 1.3rem;
}

.rows {
    padding-top: 5px;
    display: flex;
    flex-direction: column;
    gap: 7px;
}

.row {
    display: flex;
    gap: 10px;
}

.section input {
    padding: 5px 10px;
    border-radius: 8px;
    border: 1px solid #bbb;
    width: 100%;
}

.section button {
    margin-right: 10px;
    padding: 2px 8px;
    font-weight: bold;
    color: #9ef16b;
    border-radius: 5px;
    border: 1px solid #9ef16b;
    cursor: pointer;
    align-self: flex-start;
}

.profile > input {
    border-radius: 8px;
}
